import React from "react";
import onlywarsign from "./onlywarsign.png";
import {
  Dialog,
  DialogContent,
  DialogTitle,
  Button,
  Typography,
  Divider
} from "@material-ui/core";
import { battlefield, nomansland } from "./Encounters";
function getRandomIntInclusive(dice, multiplier) {
  var randomNumber = Math.floor(Math.random() * dice) + 1;
  return randomNumber; //* multiplier>0&&multiplier;
}
function IndexOfSecond(string, find) {
  let first = string.indexOf(find);

  if (first === -1) {
    return -1;
  }

  return string.indexOf(find, first + 1);
}

class App extends React.Component {
  state = {
    d100: null,
    rolltype: "none",
    action: []
  };

  searchForRolls(rollNumber, rolltype) {
    var Roll = require("roll");
    var roll = new Roll();

    let string =
      rolltype === "battlefield"
        ? battlefield(rollNumber).string
        : nomansland(rollNumber).string;
    if (string.includes("&")) {
      let workString = string;
      var whileIndex = 0;
      while (workString.includes("&")) {
        let extracted = workString.substring(
          workString.indexOf("&") - 1,
          IndexOfSecond(workString, "&") + 1
        );
        workString = workString.replace(
          extracted,
          " " +
            roll.roll(
              extracted
                .toLowerCase()
                .replace(" ", "")
                .replace("&", "")
                .replace("&", "")
            ).result
        );
        whileIndex = whileIndex + 2;
      }
      this.getAction(workString);
    } else {
      this.getAction(string);
    }
  }
  getAction(string) {
    let d10 = getRandomIntInclusive(10);

    if (string.includes("[")) {
      let array = string.split(",");
      array.forEach(item => {
        if (
          item.substring(item.indexOf("[") + 1, item.indexOf("]")).includes("-")
        ) {
          let numbers = item
            .substring(
              parseInt(item.lastIndexOf("[") + 1),
              parseInt(item.lastIndexOf("]"))
            )
            .split("-");
          if (d10 >= parseInt(numbers[0]) && d10 <= parseInt(numbers[1])) {
            let newState = this.state;
            newState.action.push(
              string.substring(0, string.indexOf("[")) +
                " " +
                item.substr(item.lastIndexOf("("), item.lastIndexOf(")"))
            );
            if (newState.action.length > 15) {
              newState.action.shift();
            }
            this.setState({
              newState
            });
          }
        } else {
          let number = item.substring(
            parseInt(item.lastIndexOf("[") + 1),
            parseInt(item.lastIndexOf("]"))
          );
          if (d10 === parseInt(number)) {
            let newState = this.state;
            newState.action.push(
              string.substring(0, string.indexOf("[")) +
                " " +
                item.substr(item.lastIndexOf("("), item.lastIndexOf(")"))
            );

            if (newState.action.length > 15) {
              newState.action.shift();
            }
            this.setState({
              newState
            });
          }
        }
      });
    } else {
      let newState = this.state;
      newState.action.push(string);
      if (newState.action.length > 15) {
        newState.action.shift();
      }
      this.setState({
        newState
      });
    }
  }
  render() {
    return (
      <Dialog fullScreen={true} open={true}>
        <DialogContent>
          <div style={{ textAlign: "center" }}>
            <img src={onlywarsign} alt="ONLY WAR" />
            <DialogTitle style={{ textAlign: "center" }}>
              Generator zdarzeń
            </DialogTitle>
          </div>
          <div style={{ width: "100%", textAlign: "center" }}>
            <Button
              color="primary"
              variant="contained"
              style={{ margin: 24 }}
              onClick={() =>
                this.searchForRolls(getRandomIntInclusive(100), "battlefield")
              }
            >
              Rzuć kością (Pole bitwy)
            </Button>
            <Button
              color="secondary"
              variant="contained"
              style={{ margin: 24 }}
              onClick={() =>
                this.searchForRolls(getRandomIntInclusive(100), "nomansland")
              }
            >
              Rzuć kością (Ziemia niczyja)
            </Button>
          </div>
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />

          {this.state.action &&
            this.state.action
              .map((item, index) => (
                <div>
                  <Typography
                    style={{
                      textAlign: "center",
                      color:
                        index === this.state.action.length - 1 ? "#000" : "#999"
                    }}
                    variant={
                      index === this.state.action.length - 1 ? "h4" : "h6"
                    }
                  >
                    {item}
                  </Typography>
                  {index !== 15 && <Divider />}
                </div>
              ))
              .reverse()}
        </DialogContent>

        <Typography
          variant="subtitle"
          style={{ position: "absolute", top: 0, left: 0, color: "#999" }}
        >
          Data provided by Blues. Coded by Dax.
        </Typography>
        <Typography
          style={{ position: "absolute", top: 0, right: 0, color: "#999" }}
          variant="subtitle"
        >
          "Only War" is the property of Black Industries{" "}
        </Typography>
      </Dialog>
    );
  }
}

export default App;
